.card {
    width: 286px;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
    margin-bottom: 20px;
}

.card-img {
    height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #868e96;
}

.card-img img {
    width: 100%;
}

.card-body {
    padding: 20px;
}

.card-title {
    font-size: 20px;
    font-weight: 500;
    line-height: 1.2;
    margin-top: 0;
    margin-bottom: 12px;
}

.card-text {
    font-size: 16px;
    line-height: 1.5;
    margin-top: 0;
    margin-bottom: 16px;
    color: #212529;
}

.btn {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: 6px 12px;
    font-size: 16px;
    line-height: 1.5;
    border-radius: 4px;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    text-decoration: none;
}

.btn:hover {
    background-color: #0069d9;
    border-color: #0062cc;
}
